package com.hclee.coffee_vote.util

import kotlinx.browser.document
import kotlinx.browser.localStorage
import kotlinx.browser.window

actual fun getCurrentUrl(): String {
    return document.URL
}

actual suspend fun getIdOrEmpty(): String {
    return localStorage.getItem("user_id").orEmpty()
}

actual suspend fun setId(id: String) {
    require(id.isNotBlank())

    localStorage.setItem("user_id", id)
}

actual suspend fun createUuid(): String {
    val uuid = UUID.v4()

    return uuid
}

/**
 * 브라우저의 뒤로가기 동작을 막는다.
 */
actual fun preventNavigateBack() {
    window.history.pushState(null, "", null)
    window.onpopstate = {
        window.history.forward()
    }
}

@JsModule("uuid")
@JsNonModule
external object UUID {

    fun v4(): String
}
