package com.hclee.coffee_vote.vote_creation

import androidx.compose.foundation.background
import androidx.compose.foundation.layout.*
import androidx.compose.foundation.rememberScrollState
import androidx.compose.foundation.shape.CornerSize
import androidx.compose.foundation.shape.RoundedCornerShape
import androidx.compose.foundation.verticalScroll
import androidx.compose.material3.*
import androidx.compose.runtime.Composable
import androidx.compose.runtime.DisposableEffect
import androidx.compose.runtime.LaunchedEffect
import androidx.compose.runtime.collectAsState
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.platform.LocalLifecycleOwner
import androidx.compose.ui.unit.dp
import androidx.compose.ui.window.DialogProperties
import androidx.lifecycle.Lifecycle
import androidx.lifecycle.LifecycleEventObserver
import com.hclee.coffee_vote.vote_detail.VoteDetailNavItem

@OptIn(ExperimentalMaterial3Api::class)
@Composable
fun VoteCreationScreen(
    modifier: Modifier = Modifier,
    viewModel: VoteCreationViewModel,
    onNavToVoteDetail: (VoteDetailNavItem) -> Unit,
) {
    val state = viewModel.state.collectAsState().value
    val lifecycleOwner = LocalLifecycleOwner.current

    DisposableEffect(lifecycleOwner) {
        val observer = LifecycleEventObserver { _, event ->
            when (event) {
                Lifecycle.Event.ON_CREATE -> {
                    viewModel.start()
                }

                else -> Unit
            }
        }

        lifecycleOwner.lifecycle.addObserver(observer)

        onDispose {
            lifecycleOwner.lifecycle.removeObserver(observer)
        }
    }
    LaunchedEffect(state) {
        state.navToVoteDetail?.getContentIfNotHandled()?.let {
            onNavToVoteDetail(it)
        }
    }

    Box(
        modifier = modifier.fillMaxSize()
            .background(
                color = MaterialTheme.colorScheme.surfaceVariant
            ),
    ) {
        Column(
            modifier = Modifier
                .fillMaxSize()
                .verticalScroll(
                    state = rememberScrollState()
                ),
            verticalArrangement = Arrangement.Center,
            horizontalAlignment = Alignment.CenterHorizontally
        ) {
            Text(text = "easy vote")
            Button(
                modifier = Modifier.padding(top = 20.dp),
                onClick = viewModel::onCreateVoteButtonClicked
            ) {
                Text(text = "create vote")
            }
            VoteCodeTextField(
                modifier = Modifier
                    .padding(top = 20.dp),
                value = state.voteCodeInput,
                label = {
                    Text("vote code")
                },
                isError = state.isVoteCodeInputError,
                onValueChange = viewModel::onVoteCodeInputChanged
            )
            Button(
                modifier = Modifier.padding(top = 20.dp),
                enabled = state.isEnterVoteButtonEnabled,
                onClick = viewModel::onEnterVoteButtonClicked
            ) {
                Text(text = "enter vote")
            }
            Text(
                text = state.icedCoffeeDescription
            )
        }

        if (state.isProgressIndicatorVisible) {
            CoffeeVoteCircularProgressIndicator(
                modifier = Modifier.fillMaxSize()
            )
        }

        if (state.isVoteCodeInvalidDialogVisible) {
            BasicAlertDialog(
                onDismissRequest = viewModel::onVoteCodeInvalidDialogDismissRequested,
                properties = DialogProperties()
            ) {
                Surface(
                    shape = RoundedCornerShape(
                        corner = CornerSize(20.dp)
                    )
                ) {
                    Column(
                        modifier = Modifier.padding(16.dp),
                        verticalArrangement = Arrangement.Center,
                        horizontalAlignment = Alignment.CenterHorizontally
                    ) {
                        Column(
                            verticalArrangement = Arrangement.spacedBy(10.dp),
                            horizontalAlignment = Alignment.CenterHorizontally
                        ) {
                            Text(
                                text = "Error",
                                style = MaterialTheme.typography.titleLarge
                            )
                            Text(
                                modifier = Modifier.padding(top = 10.dp),
                                text = "Vote code invalid",
                                style = MaterialTheme.typography.bodyMedium
                            )
                        }
                        TextButton(
                            modifier = Modifier.align(Alignment.End),
                            onClick = viewModel::onVoteCodeInvalidDialogConfirmButtonClicked
                        ) {
                            Text("Confirm")
                        }
                    }
                }
            }
        }
    }
}
